import React, { Component } from 'react'
import Boxes from '../containers/Boxes'
class App extends Component {
  render() {
    return (
      <div>
        <Boxes boxes={[{
          id:1,
          title:"A",
          items: [{id:10,title:"1",example:true}]
        }, {
            id: 2,
            title: "B",
            items: [{ id: 11, title: "2", example: true }]
          },{
            id:3,
            title:"C",
            items: [{ id: 12, title: "3", example: true }]
          },{
            id:4,
            title:"",
            items: [{ id: 1, title: "A" }, { id: 2, title: "B" }, { id: 3, title: "C" }]
          }
        ]}>
        </Boxes>
      </div>
    );
  }
}

export default App



